@font-face {
    font-family: 'HarmoniaSans';
      src: url('../fonts/HarmoniaSansProCyr-Regular.woff2') format('woff2'), /* Super Modern Browsers */
           url('../fonts/HarmoniaSansProCyr-Regular.woff') format('woff'), /* Pretty Modern Browsers */
           url('../fonts/HarmoniaSansProCyr-Regular.ttf')  format('truetype'); /* Safari, Android, iOS */
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'HarmoniaSans';
      src: url('../fonts/HarmoniaSansProCyr-Bold.woff2') format('woff2'), /* Super Modern Browsers */
           url('../fonts/HarmoniaSansProCyr-Bold.woff') format('woff'), /* Pretty Modern Browsers */
           url('../fonts/HarmoniaSansProCyr-Bold.ttf')  format('truetype'); /* Safari, Android, iOS */
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'HarmoniaSans';
      src: url('../fonts/HarmoniaSansProCyr-Black.woff2') format('woff2'), /* Super Modern Browsers */
           url('../fonts/HarmoniaSansProCyr-Black.woff') format('woff'), /* Pretty Modern Browsers */
           url('../fonts/HarmoniaSansProCyr-Black.ttf')  format('truetype'); /* Safari, Android, iOS */
    font-weight: 900;
    font-style: normal;
}

*
{
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-family: 'HarmoniaSans';
}

html,
body
{
    height: 100vh;
}

.webgl
{
    margin-top: 2%;
    margin-left: 2%;
    outline: none;
    border-radius: 20px;
    float: left;
}


.contentBox {   
    float: inline-end;
    margin-top: 2%;
    margin-left: 2%;
    padding: 2%;
    padding-bottom: 0%;
}

.contentBox h3 {
    font-weight: 900;
    font-size: 2.5rem;
    color: rgb(30, 30, 30);
}

.contentBox h4 {
    font-weight: 700;
    font-size: 1.2rem;
    color: rgb(30, 30, 30);
}

.contentBox p {
    font-weight: 400;
    font-size: 1rem;
    color: rgb(30, 30, 30);
    text-overflow: clip;
}

.socialLinks {
    margin-left: 2%;
    padding: 2%;
}

.socialLinks button {
    border-radius: 5%;
    margin-right: 2%;
    padding: 4% 10% 4% 10%;
    border: none;
    font-size: 1rem;
    font-weight: 700;
}


.behanceButton {
    background-color: #053eff;
    color: #fff;
}

.behanceButton:hover {
    background-color: #ddd;
    color: #053eff;
    cursor: pointer;
}

.linkedinButton {
    background-color: #0077b5;
    color: #fff;
}

.linkedinButton:hover {
    background-color: #ddd;
    color: #0077b5;
    cursor: pointer;
}


